var LikeBox = React.createClass({
    render: function () {
        return (<div className="like-box">{this.props.likes}</div>);
    }
});
//////////////////////////////////////////////////////////////////////////////////
var ViewBox = React.createClass({
    render: function () {
        return (<div className="ViewBox">{this.props.views}</div>);
    }
});
//////////////////////////////////////////////////////////////////////////////////
var ReadMoreBox = React.createClass({
    render: function () {
        return (<a className="ReadMore" href={this.props.url}>Подробнее</a>);
    }
});
//////////////////////////////////////////////////////////////////////////////////

//////////////////////////////////////////////////////////////////////////////////
var SomeElements = React.createClass({
    loadCommentsFromServer: function () {
        $.post({
            url: "/res/php/dbinteract.php",
            dataType: "json",
            success: function (data) {
                //console.log(data);
                this.setState({data: data});
            }.bind(this),
            error: function (xhr, status, err) {
                console.error(this.props.url, status, err.toString());
            }.bind(this)
        });
    }, // new
    getInitialState: function () {
        return {data: []};
    },
    componentDidMount: function () {
        this.loadCommentsFromServer();
    },
    render: function () {

        var dataOf = this.state.data; //Данные с сервера

        if (0 != dataOf.length) { //Проверка на ответ с сервера


            var arrDate = []; //Массив для React'a

            arrDate.push(function () { 
                 return (<h1 key={0} className="NewsHeader">Главные новости</h1>)
            }());

            for (var i in dataOf) { //Перебор объекта, превращение его в массив объектов React
                arrDate.push( //После этой конструкции создается ощущение, что отсосал сам себе
                    (function () { //А после этой...хз...мы такое еще не проходили.

                        console.log(dataOf);

                        var urls = "post.html?id=" + dataOf[i].id;

                        return (
                            <div key={"md-"+i} className="content-layer">
                                <time >{dataOf[i].date.substring(11, 16)}</time>
                                <h1>{dataOf[i].title}</h1>
                                <hr />
                                <img src={dataOf[i].mainimg}/>
                                <p>{dataOf[i].desc}</p>
                                <div className="InfoBox">
                                    <LikeBox likes={dataOf[i].like}/>
                                    <ViewBox views={dataOf[i].views}/>
                                </div>
                                <ReadMoreBox url={urls}/>
                            </div>
                        );
                    }()));

            }
        }
        return (<div>{arrDate}</div>);
    }
});
//////////////////////////////////////////////////////////////////////////////////////////////
//////////////////////////////////Additional Script//////////////////////////////////////////
function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function (m, key, value) {
        vars[key] = value;
    });
    return vars;
}
//////////////////////////////////////////////////////////////////////////////////////////////
var MainTextPlace = React.createClass({
    render: function () {
        return (<div className="MainTextPlace"><p>{this.props.text}</p></div>)
    }
});
//////////////////////////////////////////POSTS///////////////////////////////////////////////
var Articles = React.createClass({

    loadCommentsFromServer: function () {
        $.get({
            url: ("/res/php/dbinteract.php" + "?id=" + getUrlVars()['id']),
            dataType: "json",
            success: function (data) {
                console.log(data);
                this.setState({data: data});
            }.bind(this),
            error: function (xhr, status, err) {
                console.error(this.props.url, status, err.toString());
            }.bind(this)
        });
    }, // new
    getInitialState: function () {
        return {data: []};
    },
    componentDidMount: function () {
        this.loadCommentsFromServer();
    },
    render: function () {
        var dataFrom = this.state.data[0];
        console.log(dataFrom);
        if (typeof(dataFrom) != "undefined") {

            const BackStyle = {backgroundImage: 'url(' + dataFrom.mainimg + ')'};

            return (
                <article>
                    <header style={BackStyle}>
                        <div className="substrate"></div>
                        <a className="backa" href="index.html">На главную</a>

                        <h1>{dataFrom.title}</h1>
                        <span>{dataFrom.lead}</span>
                        <div className="InfoBox">
                            <LikeBox likes={dataFrom.like}/>
                            <ViewBox views={dataFrom.views}/>
                        </div>
                        <label>
                            <b>Автор: </b>{dataFrom.author}<br/>
                            <b>Опубликовано: </b>
                            <data value={dataFrom.date}>{dataFrom.date}</data>
                        </label>
                    </header>

                    <MainTextPlace text={dataFrom.text}/>

                </article>
            );
        } else {
            return (<h1>Подождите, пожалуйста.</h1>);
        }


    }


});

