///////////////////////////////////////LoginForm/////////////////////////////////////////
var LoginForm = React.createClass({
    getLogin: function () {
        let serial = $('.autorizationform').serializeArray();
        $.post({
            url: "loginOnSession.php",
            data: {login: serial[0].value, password: serial[1].value},
            dataType: "json",
            success: function (data) {
                console.log(data);
                this.setState({data: data});
            }.bind(this),
            error: function (xhr, status, err) {
                console.error(this.props.url, status, err.toString());
            }.bind(this)
        });

    },
    getInitialState: function () {
        return {data: []};
    },
    componentDidMount: function () {
        // this.loadCommentsFromServer();
    },
    render: function () {

        var dataOf = this.state.data; //Данные с сервера

        console.log(this.state.data);


        if (0 != 0) { //Проверка на ответ с сервера


            var arrDate = []; //Массив для React'a

            arrDate.push(function () {
                return (<h1 className="NewsHeader">Главные новости</h1>)
            }());

            for (var i in dataOf) { //Перебор объекта, превращение его в массив объектов React
                arrDate.push( //После этой конструкции создается ощущение, что отсосал сам себе
                    (function () { //А после этой...хз...мы такое еще не проходили.

                        console.log(dataOf);

                        var urls = "post.html?id=" + dataOf[i].id;

                        return (
                            <div className="content-layer">
                                <time>{dataOf[i].date.substring(11, 16)}</time>
                                <h1>{dataOf[i].title}</h1>
                                <hr />
                                <p>{dataOf[i].desc}</p>
                                <div className="InfoBox">
                                    <LikeBox likes={dataOf[i].like}/>
                                    <ViewBox views={dataOf[i].views}/>
                                </div>
                                <ReadMoreBox url={urls}/>
                            </div>
                        );
                    }()));

            }
            return (<div>{arrDate}</div>);
        } else {
            return (<form className="autorizationform">
                <label>Логин:</label><br/><input type="text" name="login" placeholder="Логин"/><br/>
                <label>Пароль:</label><br/><input type="text" name="password" placeholder="Пароль"/><br/>
                <input type="button" id="getLogin" value="Вход" onClick={this.getLogin}/>
            </form>);
        }
    }
});